.modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 720px;
  height: fit-content;
  margin: auto;
  background: #1C1C21;
  border: 1px solid rgba(76, 76, 255, 0.2);
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  z-index: 21;
  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%; }
  &__button {
    position: absolute;
    top: 0;
    right: 0;
    background: transparent;
    outline: none;
    border: none;
    margin: 0;
    padding: 0;
    &:hover {
      cursor: pointer; } } }
