.constructorItems {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  gap: 16px;
  flex-grow: 1;
  overflow-x: hidden;
  max-height: calc(100vh - 350px);
  &__scroll {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 16px;
    width: 100%;
    height: 100%;
    padding-right: 8px;
    overflow-x: hidden; }
  &__bunEmpty {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px 24px;
    background: #2f2f37;
    max-width: 536px;
    width: 100%;
    min-height: 80px;
    font-size: 16px;
    line-height: 24px;
    color: #F2F2F3;
    flex-grow: 1;
    opacity: .5;
    &_top {
      border-radius: 88px 88px 40px 40px; }
    &_bottom {
      border-radius: 40px 40px 88px 88px; } }
  &__ingredientsEmpty {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    width: 100%;
    height: 100%;
    opacity: .5; }

  &__dropHover {
    outline: 1px solid #8585AD; } }

.custom-scroll {
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 8px; }
  &::-webkit-scrollbar-track {
    background-color: #2F2F37; }
  &::-webkit-scrollbar-thumb {
    background-color: #8585AD;
    border-radius: 0;
    border: 3px solid #8585AD; } }
